import React, { Component } from 'react';
import { Button, Modal, ModalBody, ModalFooter} from 'reactstrap';
import { goBack, goHome, reloadPage } from '../../Utils/Utils';
import { Failure } from '../Statuses/Statuses';

class FailureModal extends Component {
    render() {
        return(
            <Modal isOpen={this.props.visible} backdrop={"static"} className={'modal-danger'}>
            <ModalBody>
              <div className="animated fadeIn pt-3 text-center">
                <Failure detailShow={true} />
              </div>
            </ModalBody>
            <ModalFooter>
              <Button color="secondary" onClick={goBack}>BACK</Button>
              &nbsp;&nbsp;
              <Button color="secondary" onClick={reloadPage}>RELOAD</Button>
              &nbsp;&nbsp;
              <Button color="secondary">LOGOUT</Button>
              &nbsp;&nbsp;
              <Button color="primary" onClick={goHome}>HOME</Button>
            </ModalFooter>
          </Modal>
      );
    }
}

export default FailureModal;
