const __SPACE__ = " ";
const __BRACKET_PAIR__ = "() ";
const __DOUBLE_COLON__ = " : ";
const __CALLED__ = "called";
const __HYPHEN__ = " - ";
const __IST__ = " (IST)";

const reloadPage = () => {
    window.location.reload();
};


const openPageInNewTab = (url) => {
    window.open(url, "_blank");
}

const getSearchResult = (tmkocAllEpisodes, searchString) => {
    let searchPositiveEpisodes = [];

    tmkocAllEpisodes.forEach((episode) => {
        if (checkIfStringContainsPattern(episode.episode, searchString)) { searchPositiveEpisodes.push(episode); }
        else if (checkIfStringContainsPattern(episode.shortDesc, searchString)) { searchPositiveEpisodes.push(episode); }

    });

    return searchPositiveEpisodes;

}

const checkIfStringContainsPattern = (string, pattern) => {
    string += "";
    pattern += "";
    string = string.toUpperCase();
    pattern = pattern.toUpperCase();

    return string.includes(pattern);
}

function humanFileSize(bytes, si=true, dp=1) {
    const thresh = si ? 1000 : 1024;
  
    if (Math.abs(bytes) < thresh) {
      return bytes + ' B';
    }
  
    const units = si 
      ? ['kB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'] 
      : ['KiB', 'MiB', 'GiB', 'TiB', 'PiB', 'EiB', 'ZiB', 'YiB'];
    let u = -1;
    const r = 10**dp;
  
    do {
      bytes /= thresh;
      ++u;
    } while (Math.round(Math.abs(bytes) * r) / r >= thresh && u < units.length - 1);

    return bytes.toFixed(dp) + ' ' + units[u];
  }


//logger 1.0
const Logger = (fileName, methodName, message = undefined, variableName = undefined, variableValue = undefined) => {
    if(localStorage.logsEnabled !== 'true') 
        return;
    
    // File.js - callApi() called : some_message : userName :  apple_cat
    let logData = fileName + __HYPHEN__ + methodName + __BRACKET_PAIR__ + __CALLED__ + __DOUBLE_COLON__;

    (message) && (logData += (message + __DOUBLE_COLON__));

    if (variableName) {
        logData += (variableName + __DOUBLE_COLON__);
        console.log(logData, variableValue);
    }
    else {
        console.log(logData);
    }
}

const goBack = () => {
    window.history.back();
}

const goHome = () => {
    window.open("/" ,"_self");
}

const isNullOrEmpty = (someString) => {
    return (someString === null || someString.length < 0);
}

const filterById = (jsonObject, id) => {
    const result = jsonObject.filter(
        jsonObject => (jsonObject['id'] == id)
    );
    return (result.length == 0) ? null : result[0];
}

const copyStringToClipboard = (someStr) => {
    //console.log('copyStringToClipboard() : someStr = ', someStr);
    // alert('clipboard added = ' + someStr);
    navigator.clipboard.writeText(someStr);
}

const findCostPer = (quantity, amount, perQuantity) => {
    const final = ( amount / quantity ) * perQuantity;
    return final;
}

export {
    reloadPage,
    getSearchResult,
    openPageInNewTab,
    humanFileSize,
    Logger,
    goBack,
    goHome,
    isNullOrEmpty,
    filterById,
    copyStringToClipboard,
    findCostPer,
    __IST__
}
