//Reminder.js

/*
 * Reminder
 *
 * List all the features :
 *  - save a repeating ( OR a non-repeating ) reminder to the server.
 */
import React, { Component } from 'react';

import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react';
import CIcon from '@coreui/icons-react';
import { requestHeaderPOST } from "../../../Rest-API/Header";
import { goHome, Logger } from '../../../Utils/Utils';
import StatusModal from '../../Modals/StatusModal';
import FailureModal from '../../Modals/FailureModal';
import { loginProcedureForLocalStorage, logoutProcedureForLocalStorage } from '../../../Utils/AuthRelatedThings';
import { BACKEND_AUTH_APP_BASE_URL } from 'src/Rest-API/EnvironmentSpecificThings';

const FILE_NAME = "Login.js";


class Login extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            isLoaded: false
        };
    }

    componentDidMount() {
        document.title = "Satanlabs - login page";
        this.setState({
            isLoaded: true
        });
        logoutProcedureForLocalStorage();
    }

    submitForm = (event) => {
        event.preventDefault();

        let reqData = {
            username: event.target.inputUsername.value,
            password: event.target.inputPazzword.value
        };

        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0
        });

        let API_URL = BACKEND_AUTH_APP_BASE_URL + '/token/generate';
        fetch(API_URL, requestHeaderPOST(JSON.stringify(reqData)))
        .then(res => res.json())
        .then(
            (result) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    loginProcedureForLocalStorage(result.data.token, result.data.validTill, reqData.username);
                    this.setState({
                        loadingModalState: 1,
                    });
                    setTimeout(() => {goHome();}, 200);
                } else if(result.code === 1003) {
                    //wrong username/password : please try again
                    this.setState({
                        loadingModalState: 2,
                    });
                    setTimeout(() => {
                        this.setState({
                            loadingModalVisible: false
                        });
                    }, 500);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    render() {
        Logger(FILE_NAME, "render", "render", "this.state", this.state);

        return (
            <div className="c-app c-default-layout flex-row align-items-center" style={{
                "backgroundColor":"#1e81b0"
            }}>
            <CContainer>
                <CRow className="justify-content-center">
                <CCol md="8">
                    <CCardGroup>
                    <CCard className="p-4">
                        <CCardBody>
                        <CForm onSubmit={this.submitForm}>
                            <h1>Login</h1>
                            <p className="text-muted">Sign In to your account</p>
                            <CInputGroup className="mb-3">
                            <CInputGroupPrepend>
                                <CInputGroupText>
                                <CIcon name="cil-user" />
                                </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput
                                placeholder="Username" 
                                autoComplete="on"
                                type="text"
                                id="inputUsername"
                                label="Username"
                                required
                            />
                            </CInputGroup>
                            <CInputGroup className="mb-4">
                            <CInputGroupPrepend>
                                <CInputGroupText>
                                <CIcon name="cil-lock-locked" />
                                </CInputGroupText>
                            </CInputGroupPrepend>
                            <CInput 
                                placeholder="Paxxword" 
                                autoComplete="on" 
                                type="password" 
                                id="inputPazzword"
                                label="Pazzword"
                                required
                            />
                            </CInputGroup>
                            <CRow>
                            <CCol xs="6">
                                <CButton color="primary" className="px-4" type='submit'>Login</CButton>
                            </CCol>
                            <CCol xs="6" className="text-right">
                                <CButton color="link" className="px-0">Forgot pazzword?</CButton>
                            </CCol>
                            </CRow>
                        </CForm>
                        </CCardBody>
                    </CCard>
                    {/* <CCard className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                        <CCardBody className="text-center">
                        <div>
                            <h2>Sign up</h2>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                            labore et dolore magna aliqua.</p>
                            <Link to="/register">
                            <CButton color="primary" className="mt-3" active tabIndex={-1}>Register Now!</CButton>
                            </Link>
                        </div>
                        </CCardBody>
                    </CCard> */}
                    </CCardGroup>
                </CCol>
                </CRow>
            </CContainer>

            <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
            <FailureModal visible={this.state.failureModalVisible} />
            </div>);
    }
}

export default Login;