import React from 'react';
import { CSpinner } from '@coreui/react';

const Failure = (props) =>
    <div className="animated fadeIn pt-3 text-center text-danger">
      <i className="fa fa-warning" style={{ color: "red", fontSize: "40px" }} />
      <h2>We're Sorry.</h2>
      {
        (props.detailShow) && (
            <div>
              An Error has occurred, please refresh / reload the page.<br/>
            </div>
        )
      }
    </div >
    ;

const Loading = () =>
    <div className="animated fadeIn text-center">
        <br />
        <div className="spinner-grow text-primary" role="status">
        </div><br />
        <div className="text-primary">L O A D I N G</div>
        {/* 
        <CSpinner color="primary" size="sm"/>
        <div className="text-primary">ðŸš€ ðŸš€ ðŸš€ Please wait. âŒ› âŒ› âŒ›</div>
         */}
        <br />
    </div>
    ;

const Success = () =>
    <div className="animated fadeIn pt-3 text-center">
        <h1 className="spinner-grow text-success" >
        </h1>
        <p className="text-success">S U C C E S S</p>
    </div>
    ;

export {
  Failure,
  Loading,
  Success
};
