const CONTENT_TYPE_JSON_STRING = "application/json";

const requestHeaderGET = () => {
    return (
        {
            method: "GET",
            headers: {
            }
        }
    )
};

const requestHeaderPOST = (data) => {
    return (
        {
            method: "POST",
            headers: {
                "Content-Type": CONTENT_TYPE_JSON_STRING
            },
            body: data
        }
    )
};

const requestHeaderFilePOST = (data) => {
    return (
        {
            method: "POST",
            headers: {
            },
            body: data
        }
    )
};

//========== WITH AUTH HEADER ==========

const requestHeaderGETWithAuth = (authToken) => {
    return (
        {
            method: "GET",
            headers: {
                "Authorization": authToken
            }
        }
    )
};

const requestHeaderPOSTWithAuth = (data, authToken) => {
    return (
        {
            method: "POST",
            headers: {
                "Content-Type": CONTENT_TYPE_JSON_STRING,
                "Authorization": authToken
            },
            body: data
        }
    )
};

const requestHeaderPUTWithAuth = (data, authToken) => {
    return (
        {
            method: "PUT",
            headers: {
                "Content-Type": CONTENT_TYPE_JSON_STRING,
                "Authorization": authToken
            },
            body: data
        }
    )
};

const requestHeaderDELETEWithAuth = (data, authToken) => {
    return (
        {
            method: "DELETE",
            headers: {
                "Content-Type": CONTENT_TYPE_JSON_STRING,
                "Authorization": authToken
            },
            body: data
        }
    )
};


export {
    requestHeaderGET,
    requestHeaderPOST,
    requestHeaderFilePOST,

    requestHeaderGETWithAuth,
    requestHeaderPOSTWithAuth,
    requestHeaderPUTWithAuth,
    requestHeaderDELETEWithAuth
 };