import React, { Component } from 'react';
import { Modal, ModalBody} from 'reactstrap';
import { Failure, Loading, Success } from '../Statuses/Statuses';

//props : visible , toggleVisibility , loadingModalState

class StatusModal extends Component {
    render() {
        let basic = <div></div>;
        if (this.props.loadingModalState == 0) {
        basic = <Loading />;
        }
        else if (this.props.loadingModalState == 1) {
        basic = <Success />;
        }
        else {
        basic = <Failure />;
        }

        return (
        <Modal isOpen={this.props.visible} backdrop={"static"} className={'modal-primary'}>
            <ModalBody>
            <div className="animated fadeIn pt-3 text-center">
                {basic}
            </div>
            </ModalBody>
        </Modal>
        );
    }
}

export default StatusModal;